.search-output-wrapper {
    display: flex;
    flex-direction: column;
    padding: 5px
}
.search-output-summary {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 60px;
    max-height: 60px;
    width: 100%;
}
.search-output-summary-book {
    width: 150px;
    text-align: center;

}
.search-output-summary-book .MuiButton-root {
    background-color: blue;
    color: white;
}
.search-output-summary-book .MuiButton-root.Mui-disabled {
    background-color: gray;
}

.search-output-summary-book .MuiButton-root:hover {
    background-color: orange;
}
.search-output-summary-selected {
    flex-grow: 100;
    display: flex;
    align-items: center;
    justify-content: center;
}
.search-output-summary-selected span {
    margin: 0 5px;
}
.bold {
    font-weight: bold;
}
.font18px{
    font-size: 18px;
}
.font14px{
    font-size: 14px;
}
.font10px{
    font-size: 10px !important;
}
.grow100 {
    flex-grow: 100;
}
.strike {
    text-decoration: line-through;
}
.search-output-results {
    display: flex;
    flex-direction: column;
}
.search-output-results-label {
    margin: 10px 0;
}
.search-output-results-table {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-height: 200px;
    min-height: 200px;
    overflow: scroll;
}
.search-output-hotels, .search-output-flights {
    min-width: 50%;
    max-width: 50%;
    max-height: 200px;
    overflow: scroll;
}
.output-item-wrapper {
    padding: 5px;
    display: flex;
    max-height: 95px;
    min-height: 95px;
    margin: 5px 5px 0 0 ;
}
.output-item-image {
    width: 85px;
    height: 85px;
    min-width: 85px;
    min-height: 85px;
    border: 1px solid lightgray;
}
.output-item-image.compactOut {
    width: 35px;
    height: 35px;
    min-width: 35px;
    min-height: 35px;
    align-self: center;
}
.output-item-details {
    display: flex;
    align-items: flex-start;
    justify-content: left;
    flex-direction: column;
    padding: 0 5px;
    width: calc(100% - 85px);
}
.output-item-details.compactOut {
    width: calc(100% - 35px);
}
.output-item-title {
    font-size: 16px;
    font-weight: bold;
    text-overflow: ellipsis;
    line-height: 16px;
    height: 20px;
    overflow: hidden;
}
.output-item-title.compactOut {
    font-size: 13px;
    line-height: 12px;
    height: 24px;
}
.output-item-description {
    height: 30px;
    overflow: scroll;
}
.output-item-description.compactOut {
    height: 40px;
}
.output-item-description div {
    font-size: 12px;
    line-height: 14px;
}
.output-item-actions {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 35px;
}
.output-item-actions.compactOut {
    height: 25px;
}
.output-item-price {
    flex-grow: 100;
}
.output-item-add {
    width: 100px;
    text-align: right;
}
.output-item-add .MuiButton-root.orangebg {
    background-color: orange;
    color: white;
    font-weight: bold;
}
.output-item-add .MuiButton-root.bluebg {
    background-color: blue;
    color: white;
}
.output-item-add.compactOut .MuiButton-root {
    padding: 4px;
    line-height: 16px;
}
